import {forEachEls} from './utility';

import defaultSwitches from "./switches";

export default function(switches, switchesOptions, selectors, fromEl, toEl, options) {
  selectors.forEach((selector)  => {
    var newEls = fromEl.querySelectorAll(selector)
    var oldEls = toEl.querySelectorAll(selector)
    if (this.log) {
      this.log("Pjax switch", selector, newEls, oldEls)
    }
    if (newEls.length !== oldEls.length) {
      var throwError = options.onDomDiffers(toEl, fromEl);
      if(throwError) {
        throw "DOM doesnâ€™t look the same on new loaded page: â€™" + selector + "â€™ - new " + newEls.length + ", old " + oldEls.length;
      }
    }

    forEachEls(newEls, function(newEl, i) {
      var oldEl = oldEls[i]
      if (this.log) {
        this.log("newEl", newEl, "oldEl", oldEl)
      }
      if (switches[selector]) {
        switches[selector].bind(this)(oldEl, newEl, options, switchesOptions[selector])
      }
      else {
        defaultSwitches.outerHTML.bind(this)(oldEl, newEl, options)
      }
    }, this)
  }, this)
}
